<template>
  <el-container class="register">
    <el-header class="register-el-header">
      <el-page-header @back="goBack" content="账号注册" style=""/>
    </el-header>
    <el-main>
      <el-form 
        class="register-form" 
        :model="registerForm" 
        :rules="rules" 
        ref="registerFormRef">
      <!-- 用户名 -->
      <el-form-item prop="username">
        <el-input placeholder="请输入用户名" v-model="registerForm.username"></el-input>
      </el-form-item>

      <!-- 性别 -->
      <el-form-item>
        <el-radio-group v-model="registerForm.sex">
          <el-radio label="男">男</el-radio>
          <el-radio label="女">女</el-radio>
        </el-radio-group>
      </el-form-item>

      <!-- 方向 -->
      <el-form-item prop="direction">
        <el-select v-model="registerForm.direction" filterable placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

      <!-- 密码 -->
      <el-form-item prop="password">
        <el-input 
        placeholder="请输入您的密码" 
        v-model="registerForm.password" 
        type="password" 
        show-password></el-input>
      </el-form-item>

      <!-- 确认密码 -->
      <el-form-item prop="psw">
        <el-input 
        placeholder="请再次输入您的密码" 
        v-model="registerForm.psw" 
        type="password" 
        show-password></el-input>
      </el-form-item>

      <!-- 按钮 -->
      <el-form-item class="btn">
        <el-button type="register" @click="register">提交</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </el-form-item>
    </el-form>
    </el-main>
    
  </el-container>
</template>

<script>
  import axios from 'axios'
  import { getAccountMsg, postNewUser, postNewPersonMsg, postNewErrorBook } from 'network/register.js'
  export default {
  name: 'RegisterInput',
  data() {
    return {
      usernameList: [],
      // 表单的数据绑定对象
      registerForm: {
        id: '',
        username: '鬼才',
        sex: '男',
        direction: '程序员',
        password: '123456',
        psw: '123456',
        power: 'common'
      },
      options: [
        {
          value: '选项1',
          label: '程序员'
        },{
          value: '选项2',
          label: '网络管理员'
        },{
          value: '选项3',
          label: '多媒体应用制作技术员'
        },{
          value: '选项4',
          label: '电子商务技术员'
        },{
          value: '选项5',
          label: '信息系统运行管理员'
        },{
          value: '选项6',
          label: '网页制作员'
        },{
          value: '选项7',
          label: '信息处理技术员'
        },{
          value: '选项8',
          label: '软件评测师'
        },{
          value: '选项9',
          label: '软件设计师'
        },{
          value: '选项10',
          label: '软件过程能力评估师'
        },{
          value: '选项11',
          label: '网络工程师'
        },{
          value: '选项12',
          label: '多媒体应用设计师'
        },{
          value: '选项13',
          label: '嵌入式系统设计师'
        },{
          value: '选项14',
          label: '计算机辅助设计师'
        },{
          value: '选项15',
          label: '电子商务设计师'
        },{
          value: '选项16',
          label: '系统集成项目管理工程师'
        },{
          value: '选项17',
          label: '信息系统监理师'
        },{
          value: '选项18',
          label: '信息安全工程师'
        },{
          value: '选项19',
          label: '数据库系统工程师'
        },{
          value: '选项20',
          label: '信息系统管理工程师'
        },{
          value: '选项21',
          label: '计算机硬件工程师'
        },{
          value: '选项22',
          label: '信息技术支持工程师'
        },{
          value: '选项23',
          label: '信息系统项目管理师'
        },{
          value: '选项24',
          label: '系统分析师'
        },{
          value: '选项25',
          label: '系统架构设计师'
        },{
          value: '选项26',
          label: '网络规划设计师'
        },{
          value: '选项27',
          label: '系统规划与管理师'
        }
      ],
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 6, message: '长度在 2 到 6 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
        psw: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6 到 15 个字符', trigger: 'blur' }
        ],
      }
    }
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    register() {
      let isExited = false
      // 若密码与确认密码一致, 则判断用户名是否已存在: 存在, 提示用户该用户名已经存在.   不存在, 拼装信息并且写入数据库
      if(this.registerForm.password === this.registerForm.psw && this.registerForm !== '') {
        for(let i of this.usernameList) {
          if(this.registerForm.username === i.username) {
              isExited = true
            }
        }
        // 用户名已被注册: isExited = true 用户名尚未被注册: isExited = false
        // 用户名已被注册: 让用户重新填写资料 
        // 用户名尚未被注册: 拼装数据, 将数据写入数据库
        if(isExited) {
          this.$message({showClose: true, message: '该用户名已存在', type: 'error'})
        }else {
          let _this = this
          axios.all([postNewUser(this.newUser()), postNewPersonMsg(this.newPersonMsg()), postNewErrorBook(this.newErrorBook())])
          .then(axios.spread(function (postNewUser, postNewPersonMsg, postNewErrorBook) {
            // 三个请求现在都执行完成
            _this.$message({showClose: true, message: '用户注册成功', type: 'success'})
            // 注册成功后跳转到登陆页面
            _this.$router.push({ path: '/' })
          }))
          // axios.all([this.newUser(), this.newPersonMsg(), this.newErrorBook()]).then(axios.spread((userMsg, personMsg, errorBookMsg) => {
          //   if(userMsg.status === 201 && personMsg.status === 201 && errorBookMsg.status === 201) {
          //     // 成功后提示用户注册成功
          //     this.$message({showClose: true, message: '用户注册成功', type: 'success'})
          //     // 注册成功后跳转到登陆页面
          //     this.$router.push({ path: '/' })
          //   }
          // }))
        }
      }else {
        this.$message({showClose: true, message: '两次输入的密码不一致, 请重新输入密码', type: 'error'})
      }
    },
    reset() {
      this.$refs.registerFormRef.resetFields();
    },
    newUser() {
      let newUser = {
        id: this.registerForm.id,
        username: this.registerForm.username,
        password: this.registerForm.password,
        power: this.registerForm.power
      }
      // 拼装新用户的账号密码信息,发送到 Register表
      return newUser
    },
    newPersonMsg() {
      let newPersonMsg = {
        id: this.registerForm.id,
        username: this.registerForm.username,
        sex: this.registerForm.sex,
        direction: this.registerForm.direction,
        power: this.registerForm.power,
        scoreInfo: []
      }
      // 拼装新用户的个人信息, 发送到 PersonMessage表
      return newPersonMsg
    },
    newErrorBook() {
      let newErrorBook = {
        id: this.registerForm.id,
        username: this.registerForm.username,
        totalError: 0,
        errorSet: []
      }
      //拼装新用户错题本信息, 发送到 ErrorBook表
      return newErrorBook
    }
  },
  created() {
    getAccountMsg().then(res => {
      this.usernameList = res
    })
  }
}
</script>

<style scoped>
  .register {
    width: 440px;
    height: 520px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .register-el-header {
    margin: 20px 32px;
  }
  .register-form {
    padding: 0 54px;
  }
  .register-form h2 {
    text-align: center;
  }
  .btn {
    display: flex;
    justify-content: flex-end;
  }
  .btn button{
    width: 72px;
    height: 40px;
  }
</style>