import { request } from './request';

export function getAccountMsg() {
  return request({
    url: '/Register'
  })
}

export function postNewUser(data) {
  return request({
    method: 'post',
    url: '/Register',
    data
  })
}

export function postNewPersonMsg(data) {
  return request({
    method: 'post',
    url: '/PersonMessage',
    data
  })
}

export function postNewErrorBook(data) {
  return request({
    method: 'post',
    url: '/ErrorBook',
    data
  })
}